import React, { useMemo } from "react";
import { Dayjs } from "dayjs";
import { useDayjsLocale } from "../../../../locale";
import { DateController, isSameMonth } from "../../date-util";
import { PanelBody } from "../PanelBody";
import type { IMonthBodyProps } from '../interface';

const MONTH_COL_COUNT = 3;
const MONTH_ROW_COUNT = 4;

export const MonthBody: React.FC<IMonthBodyProps> = React.memo((props) => {
  const { value, viewDate, onSelect, disabledDate } = props;
  const dayjsLocale = useDayjsLocale();

  const months: string[] = useMemo(() => DateController.locale.getShortMonths(dayjsLocale),[dayjsLocale]);
  const baseMonth = DateController.setMonth(viewDate, 0);

  return (
    <PanelBody
      viewDate={viewDate}
      rowNum={MONTH_ROW_COUNT}
      colNum={MONTH_COL_COUNT}
      baseDate={baseMonth}
      getCellDate={DateController.addMonth}
      getCellText={(date) => months[DateController.getMonth(date)]}
      onSelect={onSelect}
      disabledDate={disabledDate}
      mode="month"
      getCellClassName={(curDate: Dayjs) => [
        'month',
        value && isSameMonth(curDate, value) ? 'selected' : undefined,
      ]}
    />
  );
});
